@mixin border-colored($color) {
  border: solid $color $standard-border-width;
}

@each $color-name, $color-value in $colors {
  .border-#{$color-name} {
    @include border-colored($color-value);
  }
}

@mixin border-top-colored($color) {
  border-top: solid $color $standard-border-width;
}

@mixin border-bottom-colored($color) {
  border-bottom: solid $color $standard-border-width;
}

@each $color-name, $color-value in $colors {
  .border-top-#{$color-name} {
    @include border-top-colored($color-value);
  }
}

@each $color-name, $color-value in $colors {
  .border-bottom-#{$color-name} {
    @include border-bottom-colored($color-value);
  }
}

@each $color-name, $color-value in $colors {
  .border-#{$color-name}-hover {
    @include border-colored($transparent);

    &:hover {
      @include border-colored($color-value);
    }
  }
}

.border-none {
  border: none;
}

//// DARK MODE STYLES ////

.dark-mode {

  .border-black {
    @include border-colored($light-grey)
  }

  .border-white {
    @include border-colored($black)
  }

  .border-medium-grey {
    @include border-colored($dark-grey)
  }

  .border-dark-grey {
    @include border-colored($light-grey)
  }

  .border-dark-red {
    @include border-colored($light-red)
  }

  .border-light-red {
    @include border-colored($dark-red)
  }

}